"use clie"

import React from "react";
import Image from "next/image";
import Link from "next/link";
import {UserButton , SignInButton , useUser , SignOutButton} from '@clerk/nextjs'
import { IoMdContact } from "react-icons/io";

const Navbar: React.FC = () => {

    const { isSignedIn } = useUser();

  return (
    <nav className="flex justify-between items-center p-6 bg-[#1E1E1E] text-gray-300 h-[100px] border-b-2 border-[#342c16]">

      {/* Logo */}
      <div className="logo">
        <Image src="/logo.png" alt="Classic Refuge Logo" width={50} height={50} />
      </div>

      {/* Navigation Links */}
      <div className="nav-links space-x-6 text-lg">
        <Link href="/" className="hover:text-[#C1A25D] transition">HOME</Link>
        <Link href="/about" className="hover:text-[#C1A25D] transition">ABOUT</Link>
        <Link href="/booking" className="hover:text-[#C1A25D] transition">BOOKING</Link>
        <Link href="/barbers" className="hover:text-[#C1A25D] transition">BARBERS</Link>
        <Link href="/services" className="hover:text-[#C1A25D] transition">SERVICES</Link>
      </div>

      {/* Profile Icon */}


      {isSignedIn ? (      <div className="profile">
     <SignInButton >
     <IoMdContact className="h-8 w-8 hover:text-[#C1A25D]"/>
     </SignInButton>
      </div>) : (
        <div>
        <UserButton />
        <SignOutButton/>
        </div>
      )}

    </nav>
  );
};

export default Navbar;
